import React, { useState, useEffect } from 'react';
import svgHover from 'utils/svgHover';
import svg from 'svg/face'

const Face = () => {
  const [isReady, setIsReady] = useState(false);

  useEffect(() => {
    !isReady && setIsReady(true); // ready after first render
    isReady && svgHover();
  }, [isReady]);

  return (
    <section id='face' className='section section--face'>
      <div className='face-wrapper' dangerouslySetInnerHTML={{__html: svg}} />
    </section>
  )
};

export default Face;