export default () => {
  const wrapper = document.querySelector('#polygon-A');
  const opacityOver = 0;
  const opacityOut = 1;
  const previousTargets = [];

  const mouseOverEffect = function(e) {
    const t = e.target;

    if (t && t.nodeName === 'polygon') {
      t.style.opacity = opacityOver;
      
      if(t.nextElementSibling && t.nextElementSibling.nextElementSibling) {
        t.nextElementSibling.nextElementSibling.style.opacity = opacityOver;
        previousTargets.push(t.nextElementSibling.nextElementSibling);
      }
      
      if(t.previousElementSibling && t.previousElementSibling.previousElementSibling) {
        t.previousElementSibling.previousElementSibling.style.opacity = opacityOver;
        previousTargets.push(t.previousElementSibling.previousElementSibling);
      }

      if(t.nextElementSibling) {
        t.nextElementSibling.style.opacity = opacityOver;
        previousTargets.push(t.nextElementSibling);
      }
      
      if(t.previousElementSibling) {
        t.previousElementSibling.style.opacity = opacityOver;
        previousTargets.push(t.previousElementSibling);
      }

      previousTargets.push(t);
      


      // this interval will loop through the targets array
      // and unset the opacity one by one
      const polygonShifter = setInterval(function() {
        if(previousTargets.length) {
          const shifted = previousTargets.shift();
          shifted.style.opacity = opacityOut;
        } else {
          clearInterval(polygonShifter);
        }
      }, 300);
    }
    
  };

  wrapper.addEventListener('mouseover', mouseOverEffect, false)
  wrapper.addEventListener('touchstart', mouseOverEffect, false)
}